Vue.component('page-header', {
  template: '#page-header-template',
  data() {
    return {
      menuActive: false,
      nowBoxGroup: '',
    }
  },
  props: {
    menus: {
      type: Object,
      default: function() {
        return {
              spLinks: [
                { legend: '會員登入', link: '#', bgPad: true },
                { legend: '聯繫我們', link: '#' }
              ],
              language: {
                legend: '語言',
                nodes: [
                  { legend: 'ENGLISH', link: '#' },
                  { legend: '简体中文', link: '#' },
                  { legend: '繁体中文', link: '#' }
                ]
              },
              boxGroups: [
                {
                  legend: '關於我們',
                  link: 'about_us.html',
                  target: '_blank',
                  nodes: [
                    { legend: '我們的優勢', link: 'about_us.html', target: '_blank' },
                    { legend: '全球未來發展', link: 'about_us.html' },
                    { legend: '我們的成員', link: 'about_us.html' }
                  ]
                },
                {
                  legend: '理財項目',
                  nodes: [
                    { legend: '外匯產品', link: '#' },
                    { legend: '與MS交易比特幣', link: '#' },
                    { legend: '交易平台', link: '#' }
                  ]
                },
                {
                  legend: '交易培訓',
                  nodes: [
                    { legend: '新手入門', link: '#' },
                    { legend: '外匯市場特點', link: '#' },
                    { legend: '直通式处理交易经纪商 (STP)', link: '#' },
                    { legend: '差价合约', link: '#' },
                    { legend: '基本知識', link: '#' }
                  ]
                }
              ]
            }
      }
    },
    menuTitle: {
      type: String,
      default: '選單'
    }
  },
  methods: {
    getOnopened(data) {
      this.nowBoxGroup = data
    },
    menuAction() {
      this.menuActive = !this.menuActive
      if(this.menuActive) return scrollToY({});
    }
  }
})


Vue.component('box-group', {
  template: '#box-group-template',

  data() {
    return {
      active: false
    }
  },

  props: {
    legend: {
      type: String,
      default: "標題"
    },
    link: String,
    target: String,
    legendLink: {
      type: String,
      default: "#"
    },
    nodes: Array,
    appendClass: String
  },

  methods: {
    onMouseenter() {
      this.active = !this.active
      if( this.active ) {
        this.$emit('opened', this.legend);
      }
    },
    onMouseleave() {
      this.active = false
    }
  }
})
