Vue.component('slider-box', {
  template: '#slider-box-template',
  data() {
    return {
      nowNode: 0,
      nodeClass: 'next',
      intervalDude: {}
    }
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    speed: {
      type: Number,
      default: 3000
    }
  },
  computed: {
    nodesSize: function() {
      return this.nodes.length
    }
  },
  mounted: function() {
    this.$nextTick( () => {
      this.routine()

      var hammertime = new Hammer(this.$el);
      hammertime.on("swipeleft", (e) => {
        if(e.deltaX <= -150) return this.nodeAction({action: 'next'})
      });
      hammertime.on("swiperight", (e) => {
        if(e.deltaX >= 150) return this.nodeAction({action: 'last'})
      });
    })
  },
  methods: {
    routine() {
      this.stopRoutine()
      this.intervalDude.action = setTimeout(() => {
        this.nodeAction({setter: this.nowNode})
      }, this.speed)
    },
    stopRoutine() {
      clearTimeout(this.intervalDude.action)
    },
    pageChanger({page = 0, method='next'}) {
      this.stopRoutine()
      this.nowNode = page
      this.nodeClass = method
      this.routine()
    },
    nodeAction({setter = this.nowNode, action = 'next'}) {
      let page, method
      action = action.trim().toLowerCase()
      if(action == 'click') {
        method = setter > this.nowNode ? 'next' : 'last'
        return this.pageChanger({page: setter, method})
      }
      if(action == 'next') {
        page = setter >= this.nodesSize - 1 ? 0 : setter + 1
        return this.pageChanger({page, method: action})
      }
      if( action == 'last' ) {
        page = setter <= 0 ? this.nodesSize - 1 : setter - 1
        return this.pageChanger({page, method: action})
      }
    }
  }
})
