Vue.component('content-nav', {
  template: '#content-nav-template',
  data() {
    return {
      isNavOpen: false
    }
  },
  props: {
    nodes: {
      type: Array,
      required: false
    },
    now: {
      type: Number,
      default: 0
    }
  },
  methods: {
    navAction: function() {
      this.isNavOpen = !this.isNavOpen
    },
    delay: function(i) {
      let out = i >= this.now ? i - 1 : i;
      out *= .1
      return this.isNavOpen ? out.toString() + 's' : '0'
    }
  }
})
